export default {
	data() {
		return {
			mainData: {}
		};
	},
	onShow() {
		this.$store.commit('judgeLogin'); //判断登录状态
	},
	onLoad() {
		this.getUserData();
	},
	methods: {
		dump(url) {
			uni.navigateTo({
				url: url
			});
		},
		loginOut() {
			//退出登录
			this.$store.commit('logout'); //判断登录状态
			this.dump('/pages/member/login')
		},
		getUserData() {
			//获取用户信息
			var _this = this;
			_this.$request.user
				.getUserData({})
				.then(data => {
					if (data.status == 1) {
						_this.mainData = data.data;
					} else {
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
				})
				.catch(err => {
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});
		}
	}
};
